import React, { useEffect, useRef, useState } from 'react';
import { Client } from '@stomp/stompjs';
import SockJS from 'sockjs-client';
import LoadingComponent from './LoadingComponent';
import ErrorMessageComponent from './ErrorMessageComponent';
import ServiceManagementComponent from './ServiceManagementComponent';

const WebSocketComponent = (props) => {
  const [deployments, setDeployments] = useState([]);
  const [scales, setScales] = useState({});
  const [status, setStatus] = useState({});
  const [loading, setLoading] = useState(true);
  const [loadingButtons, setLoadingButtons] = useState({}); // Buton yükleme durumları
  const clientRef = useRef(null);

  useEffect(() => {
    const client = new Client({
      webSocketFactory: () => new SockJS('http://localhost:8080/ws'),
      reconnectDelay: 5000,
      heartbeatIncoming: 10000,
      heartbeatOutgoing: 10000,

      onConnect: () => {
        console.log('WebSocket bağlantısı kuruldu.');
        client.subscribe('/topic/status', (message) => {
          //console.table(loadingButtons);
          const newDeployments = JSON.parse(message.body);
          setDeployments((prev) => {
            const old = { ...prev };
            const buttonsLoadings = {};
            newDeployments.forEach((deployment) => {
              const previousDeployment = Object.values(old).find(
                (item) => item.name === deployment.name
              );
              if(previousDeployment){
                console.table(previousDeployment);
                if (previousDeployment["status"] !== deployment.status) {
                  console.log(deployment.name+": sonunda loading false çekiliyor..");
                  console.table(previousDeployment);

                  buttonsLoadings[deployment.name] = false;
                }

              }
              else{
                //console.table(previousDeployment);
                //console.log("Old Object: ", old);
                //console.table(old);
                console.log(deployment.name+": ilk çalışma loading false çekiliyor..");
                buttonsLoadings[deployment.name] = false;
              }
            }
          );
            setLoadingButtons(buttonsLoadings);
            return newDeployments;
          });

          //setDeployments(newDeployments);

          const newScales = {};
          const newStatuses = {};
          newDeployments.forEach((deployment) => {
            newScales[deployment.name] = deployment.readyReplicas;
            newStatuses[deployment.name] = deployment.status;
          });

          setScales(newScales);
          setStatus(newStatuses);
          setLoading(false);

          // Scale işlemi tamamlandıysa loading durumunu kapat
          /*setLoadingButtons((prev) => {
            const updated = { ...prev };
            newDeployments.forEach((deployment) => {
              if (updated[deployment.name]) {
                updated[deployment.name] = false;
              }
            });
            return updated;
          });*/
        });
      },
    });

    client.activate();
    clientRef.current = client;

    return () => {
      if (client.connected) {
        client.deactivate();
      }
    };
  }, []);

  const scaleService = async (name) => {
    try {
      
      setLoadingButtons((prev) => ({ ...prev, [name]: true })); // Buton yükleme durumu aktif
      console.log(name+": true");
      const newScale = scales[name] >= 1 ? 0 : 1;

      await fetch(`http://localhost:8080/${name}-deployment/scale/${newScale}`, {
        method: 'POST',
      });
    } catch (error) {
      console.error(error);
      //setLoadingButtons((prev) => ({ ...prev, [name]: false })); // Hata durumunda yükleme durumu kapat
    }
  };

  return (
    <>
      <LoadingComponent loading={loading} opacity="1" />
      <ErrorMessageComponent componentId={props.componentId} opacity="1" />
      <ServiceManagementComponent
        id={props.componentId}
        informationOfServices={deployments}
        scaleService={scaleService}
        loadingButtons={loadingButtons}
        serviceStatus={status}
      />
    </>
  );
};

export default WebSocketComponent;
