import React from 'react';
import { Button, CircularProgress, Typography } from '@mui/material';

const ServiceManagementComponent = ({ informationOfServices = [], scaleService, loadingButtons = [], serviceStatus = [] }) => {
  return (
    <div>
      {informationOfServices.map((deployment) => (
        <div key={deployment.name} style={{ margin: '10px 0' }}>
          <Typography variant="h6">
            {deployment.name} - Status: {serviceStatus[deployment.name] === 'UP' ? '🟢 UP' : '🔴 DOWN'}
          </Typography>

          {/* Eğer buton loading durumundaysa CircularProgress göster */}
          {loadingButtons[deployment.name] ? (
            <CircularProgress size={24} />
          ) : (
            <Button
              variant="contained"
              color="primary"
              onClick={() => scaleService(deployment.name)}
            >
              {deployment.readyReplicas >= 1 ? 'Scale Down' : 'Scale Up'}
            </Button>
          )}
        </div>
      ))}
    </div>
  );
};

export default ServiceManagementComponent;
